.ui.grid .photo.column {
  display: flex;
  flex-wrap: wrap;
  align-items: stretch;
  justify-content: center;
}
.ui.grid .photo.column.selected .photo.image img {
  box-sizing: border-box;
  border: 0.2em solid #2185D0;
  margin: -0.05em;
}
.ui.grid .photo.column .ui.photo.segment {
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-direction: column;
  padding: 0;
}
.ui.grid .photo.column .ui.photo.segment img {
  box-shadow: 0 2px 4px 0 rgba(34, 36, 38, 0.12), 0 2px 10px 0 rgba(34, 36, 38, 0.08);
}
.ui.grid .photo.column .ui.photo.segment .photo.image {
  min-height: 100px;
  display: flex;
  align-items: center;
}
.ui.grid .photo.column .ui.photo.segment .photo.title {
  align-self: center;
}
#photosGrid {
  min-height: 300px;
}
#photosDetail .image {
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 1em;
}
#photosDetail.fixed .raised.segments {
  margin-top: 1em;
}
#albumToolbar.fixed {
  border-top-left-radius: 0;
  border-top-right-radius: 0;
  border-top-width: 0;
}
#albumToolbar.fixed + div {
  margin-top: 70px;
}
.drop.indicator .photo.title {
  color: rgba(0, 0, 0, 0.87);
}
